* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.App {
  text-align: center;
}

.parent {
  background: rgb(2, 0, 36);
  background: linear-gradient(
    90deg,
    rgba(2, 0, 36, 1) 0%,
    rgba(14, 9, 121, 1) 35%,
    rgba(0, 212, 255, 1) 100%
  );
  color: wheat;
}

.admin-form {
  max-width: 25%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100vh;
}

.admin-form > input {
  padding: 10px 20px;
  border-radius: 10px;
  margin-bottom: 10px;
}

.admin-form > label {
  font-size: 15px;
  font-weight: 900;
}

.admin-form > button {
  padding: 5px 10px;
  color: #fff;
}

.child-container {
}
.child-container > h2 {
  display: flex;
  margin-left: 20px;
}

.child-container > button {
  padding: 10px;
  color: #fff;
}

.grandChild-container {
  display: flex;
  height: 100vh;
  position: relative;
}

.grandChild-container > img {
  width: auto;
  height: 90%;
}

.grandChild-container > h2 {
  position: absolute;
  top: 90%;
  left: 30%;
}

.grandChild-container > p {
  position: absolute;
  top: 56%;
  left: 30%;
}

.container-button {
  padding: 10px 20px;
  position: absolute;
  top: 50%;
  color: #fff;
  border-radius: 10px;
  background-color: crimson;
}

@media screen and (max-width: 500px) {
  .grandChild-container {
    display: flex;
    height: 100vh;
    position: relative;
    background-color: black;
  }

  .grandChild-container > img {
    width: auto;
    height: 90%;
  }

  .grandChild-container > h2 {
    position: absolute;
    top: 90%;
    left: 0%;
    font-size: 15px;
  }

  .grandChild-container > p {
    position: absolute;
    font-size: 15px;
    top: 90%;
    left: 10%;
  }
  .container-button {
    padding: 10px 20px;
    position: absolute;
    top: 90%;
    left: 20%;
    color: #fff;
    font-size: 10px;
    border-radius: 10px;
    background-color: crimson;
  }

  .container > form {
    width: 100%;
    border-radius: 5px;
    background: #fff;
    padding: 80px 90px;
  }

  .field > label {
    font-size: 20px;
  }

  .field > input {
    padding: 10px 20px;
    border-radius: 10px;
    /* margin: 10px; */
  }

  .ui-form > button {
    padding: 10px;
    border-radius: 10px;
    color: #fff;
    margin-top: 10px;
  }
  pre {
    width: 70%;
    color: #fff;
    font-size: larger;
  }

  button {
    background: #0563b4 !important;
  }
  p {
    color: red;
  }
}

